import request from '@/utils/request'

/* 调查子表列表查询 */
export const getSonTableList = data => request({ url: '/dcxx/dicIndexItem/getSonTableList', method: 'post', data })
/* 保存调查子表字段值的信息 */
export const saveFiled = data => request({ url: '/dcxx/dicIndexItem/saveFiled', method: 'post', data })
/* 根据调查子表id获取字段信息 */
export const getFiledList = data => request({ url: '/dcxx/dicIndexItem/getFiledList', method: 'post', data })
/* 获取调查表列表创 */
export const getSurveyTableList = data => request({ url: '/dcxx/dicIndexItem/getSurveyTableList', method: 'post', data })
/* 查看具体调查表信息 */
export const getTableInfo = data => request({ url: '/dcxx/dicIndexItem/getTableInfo', method: 'post', data })
/* 查询表字段信息创 */
export const getFileds = data => request({ url: '/dcxx/dicIndexItem/getFileds', method: 'post', data })
/* 修改排序值 */
export const editSort = data => request({ url: '/dcxx/dicIndexItem/editSort', method: 'post', data })
/* 调查子表修改 */
export const updateSonTable = data => request({ url: '/dcxx/dicIndexItem/updateSonTable', method: 'post', data })
/* 删除表字段创 */
export const deleteFileds = data => request({ url: '/dcxx/dicIndexItem/deleteFileds', method: 'post', data })
/* 添加删除排序调查表字段 */
export const save = data => request({ url: '/dcxx/dicIndexItem/save', method: 'post', data })
