<template>
  <div id="tablet">
    <QueryBox @searchSrldId="searchSrldId" >
      <Button type="primary" size="small" class="btn-exportBtn"><i class="iconfont icon-xiazai"></i>专题报告下载</Button>
    </QueryBox>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from './reportAnalysisTable_query'
import {
  // 表格列表
  getSurveyTableList,
  // 删除单条记录---暂时
  infoDel
} from '@/api/dataCollection'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '行政区划',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '专题报告名称',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '专题报告类型',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '操作',
          key: 'address',
          width: 170,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    console.log('查看')
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.infoId = params.row.id
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该报告吗？',
                      onOk: () => {
                        this.infoDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false
    }
  },
  components: {
    QueryBox
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    obj () {
      this.getTableList()
    }
  },
  methods: {
    /* 接受查询框查询条件 */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getSurveyTableList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          param: this.obj.param,
          redlineName: this.obj.srldName,
          tableName: this.obj.param
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      infoDel({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
  }
</style>
